*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

header
{    
    background-color: black;
    position: sticky;
    top:0px;
    div
    {
        display :flex;
        flex-direction: row;     
        justify-content: center;
        a
        {
            color: white;
            text-decoration: none;            
            font-size: 2em;
            margin-left:5%; 
            margin-right:5%;
        }
    }
     
}

section.accueil article
{
    border: 2px solid black;
    margin:5px;
    .card
    {
        display :flex;
        flex-direction: column;        
        justify-content: end;
        text-align: center;
        border: 2px solid black;
        margin:5px;
        a
        {
            text-decoration: none;
        }
        .image
        {
            display: block;
            width: 100%;
        }
        .container
        {   
            h4,p
            {
                padding:5px;
                border:1px solid black;
            }
        }
    }
}

section.article article 
{
    border: 2px solid black;
    margin: 5px;
    .image
    {
        width:100%;
        display: block;
    }
    p
    {
        background-color: white;
        padding:5px;
        margin:5px;
        border:2px solid black;
    }
    #contenu{        
        text-align: justify ;        
    }
}

aside
{
    border: 2px solid black;    
    margin: 5px;
    .image
    {        
        width:100%;
        display: block;
    }
    ul
    {
        list-style:none;
        list-style-position: inside;
        margin:10px;
        border: 2px solid black; 
        ul 
        {
            border: none;
            list-style:inside;
        }
    }
}

@media all and (min-width: 768px) 
{
    section.accueil article
    {
        display : grid;
        grid-template-columns: repeat(2, 1fr);
    }
    section.accueil aside, section.article aside
    {
        display : flex;
        flex-direction: row;        
      
    }  
    
}

@media all and (min-width: 1024px) 
{
    section.accueil
    {
        display : grid;
        grid-template-columns: 3fr 1fr ;
        article
        {
            display : grid;
            grid-template-columns: repeat(3, 1fr);            
        }
        aside
        {
            display : flex;
            flex-direction: column;        
        }
    }
    
    section.article
    {
        display : flex;
        flex-direction: row-reverse;        
        article
        {
            width: 75%;           
        }
        aside
        {
            width: 25%;
            display : flex;
            flex-direction: column; 
         
        }
    }
}